<template>
  <!-- 我的收藏   歌手列表 -->
  <div class="artistlist">
    <!-- 头部 -->
    <div class="artistlist__head">
      <div class="artistlist__head__title">收藏的歌手 <span class="artistlist__head__title__count">({{ count }})</span></div>
      <div class="artistlist__head__search"></div>
    </div>
    <!-- 歌手列表 -->
    <div class="artistlist__list">
      <div v-for="(item, index) in ArtistList"
      :class="{artistlist__list__item: true, 'artistlist__list__item-dark': index%2==0}"
      :key="item.id"
      @click="$router.push(`/SingerDetail/${item.id}`)">
            <img :src="item.picUrl" alt="" class="artistlist__list__item__pic">
            <span class="artistlist__list__item__item">{{ item.name }}</span>
          <div class="artistlist__list__item__albumcount">专辑：{{ item.albumSize}}</div>
          <div class="artistlist__list__item__mvcount">MV：{{ item.mvSize }}首</div>
      </div>
    </div>
  </div>
</template>

<script>
import { getArtist } from '../../api/api_mycollect'
export default {
  name: 'ArtistList',
  data () {
    return {
      ArtistList: null,
      count: null
    }
  },
  methods: {
    async getData () {
      const res = await getArtist()
      this.count = res.data.count
      this.ArtistList = res.data.data
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.artistlist{
  &__head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
    &__title{
      font-size: 14px;
      font-weight: 500;
      &-count{
        font-size: 14px;
        color: #e1e1e1;
      }
    }
  }
  &__list{
      &__item{
        display: flex;
        align-items: center;
        padding: 10px 0;
        background-color: #ffffff;
          &__pic{
            margin-right: 10px;
            height: 60px;
            width: 60px;
            border-radius: 5px;
          }
          &__item{
            flex: 1;
            font-size: 12px;
            height: 60px;
            line-height: 60px;
            color: #111111;
          }
        &__albumcount{
          width: 200px;
          font-size: 12px;
          color: #9c9c9c;
        }
        &__mvcount{
          width: 90px;
          font-size: 12px;
          color: #9c9c9c;
        }
      }
      &__item-dark{
        background-color: #fafafa;
      }
      &__item:hover{
        background-color: #f1f2f3;
      }
    }
}
</style>
